import './wizard.css';
import 'antd/dist/antd.css';
import React from "react";
import {
    PageHeader,
    Form,
    Input,
    Checkbox,
    Button,
    Modal
} from 'antd';
import { RequestTool } from "./utils/requestTool";

export default class RegisterWizard extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            username: '',
            password: '',
            re_password: '',
            cellphone: '',
            agreeable: false
        };
    }
    callBackend() {
        let break_reason = this.checkInput();
        if (break_reason !== '') return Modal.warning({
            title: '',
            content: break_reason,
            okText: '',
        });
        console.log(this.state);
        RequestTool.post(atob('aHR0cHM6Ly9hcGkuZHNjaXRlY2guY29tL2NnaS1iaW4vcmVnaXN0ZXIuY2dp'), this.state).then((res) => {
            switch (res) {
                case 'OK':
                    this.setState({
                        username: '',
                        password: '',
                        re_password: '',
                        cellphone: '',
                        agreeable: false
                    });
                    Modal.success({
                        title: '',
                        content: '',
                        okText: '',
                        onOk: args => {
                            window.history.go(-1);
                        }
                    });
                    break;
                case 'IllegalRequest':
                    this.setState({
                        username: '',
                        password: '',
                        re_password: '',
                        cellphone: ''
                    });
                    Modal.warning({
                        title: '',
                        content: '',
                        okText: '',
                    });
                    break;
                case 'InvalidRequest':
                    this.setState({
                        username: '',
                        password: '',
                        re_password: '',
                        cellphone: ''
                    });
                    Modal.info({
                        title: '',
                        content: '',
                        okText: '',
                    });
                    break;
                case 'UserAlreadyExists':
                    Modal.warning({
                        title: '',
                        content: '',
                        okText: '',
                    });
                    break;
                case 'InternalNotAllowed':
                    Modal.info({
                        title: '',
                        content: '',
                        okText: '',
                    });
                    break;
                default:
                    this.setState({
                        username: '',
                        password: '',
                        re_password: '',
                        cellphone: ''
                    });
                    Modal.error({
                        title: '',
                        content: '',
                        okText: '',
                    });
                    break;
            }
            console.log(res);
        }).catch((e) => {
            console.log(e);
        });
    }
    checkInput() {
        if (this.state.username === '') return '';
        if (this.state.password === '' || this.state.re_password === '') return '';
        if (this.state.password.length <= 6 || this.state.re_password.length <= 6) return '7';
        if (this.state.password !== this.state.re_password) return '';
        if (this.state.cellphone.length !== 11) return '';
        if (!this.state.agreeable) return '';
        let cn_PhoneRule_CM = new RegExp('^((13[4-9])|(14[7-8])|(15[0-2,7-9])|(165)|(178)|(18[2-4,7-8])|(19[5,7,8]))\\d{8}|(170[3,5,6])\\d{7}$');
        let cn_PhoneRule_CU = new RegExp('^((13[0-2])|(14[5,6])|(15[5-6])|(16[6-7])|(17[1,5,6])|(18[5,6])|(196))\\d{8}|(170[4,7-9])\\d{7}$');
        let cn_PhoneRule_CT = new RegExp('^((133)|(149)|(153)|(162)|(17[3,7])|(18[0,1,9])|(19[0,1,3,9]))\\d{8}|((170[0-2])|(174[0-5]))\\d{7}$');
        let cn_PhoneRule_CBN = new RegExp('^((192))\\d{8}$');
        return !(cn_PhoneRule_CM.test(this.state.cellphone) || cn_PhoneRule_CU.test(this.state.cellphone) || cn_PhoneRule_CT.test(this.state.cellphone) || cn_PhoneRule_CBN.test(this.state.cellphone)) ? '' : '';
    }
    render() {
        return (
            <div className="App">
                <PageHeader
                    style={{
                        border: '0px',
                    }}
                    onBack={() => {
                        window.history.go(-1);
                    }}
                    title=""
                    subTitle="Registration"
                />
                <Form>
                    <Form.Item label="">
                        <Input onChange={(e) => { this.setState({ username: e.target.value }) }} value={this.state.username} />
                    </Form.Item>
                    <Form.Item label="" hasFeedback>
                        <Input.Password onChange={(e) => { this.setState({ password: e.target.value }) }} value={this.state.password} />
                    </Form.Item>
                    <Form.Item label="" hasFeedback>
                        <Input.Password onChange={(e) => { this.setState({ re_password: e.target.value }) }} value={this.state.re_password} />
                    </Form.Item>
                    <Form.Item label="">
                        <Input onChange={(e) => { this.setState({ cellphone: e.target.value }) }} value={this.state.cellphone} />
                    </Form.Item>
                    <Form.Item>
                        <Checkbox onChange={(e) => { this.setState({ agreeable: e.target.checked }) }} checked={this.state.agreeable}>
                            &nbsp;<a href="./#" target="_self"></a>
                        </Checkbox>
                    </Form.Item>
                    <Form.Item>
                        <ul style={{
                            color: '#666',
                            fontSize: '10px',
                            listStyle: 'square',
                            marginBottom: '0px',
                            padding: '0px'
                        }}>
                            <li style={{listStyle: 'none'}}></li>
                            <li></li>
                            <li></li>
                            <li></li>
                        </ul>
                    </Form.Item>
                    <Form.Item>
                        <Button type="primary" htmlType="submit" onClick={this.callBackend.bind(this)}>
                            
                        </Button>
                        &nbsp;
                        <Button type="danger" htmlType="reset">
                        
                        </Button>
                    </Form.Item>
                </Form>
            </div>
        )
    }
}